import React, { Component } from 'react'
import VoiceRecognition from './VoiceRecognition.js'

class VoiceRecognitionDemo extends Component {
  constructor(props, context){
        super(props, context)
        this.onEnd = this.onEnd.bind(this)
        this.onResult = this.onResult.bind(this)
        this.start = this.start.bind(this)
        this.stop = this.stop.bind(this)
        this.state = {
            start: false,
            stop: false,
            transcript: null,
        }
    }

  onEnd() {
        this.setState({ start: false, stop: false });
        // this.props.action('end')();
    }

  onResult(args)  {
        const finalTranscript = args.finalTranscript 
        const result = finalTranscript;

        if(this.state.transcript != finalTranscript){
            this.setState({ transcript: finalTranscript });
            console.log(finalTranscript);
        }

        // this.setState({ start: false });
        // console.log(finalTranscript,"FT")
        // this.props.action('result')(finalTranscript);
    }

  start(){
        console.log('sadasd');
      this.setState({start:true})
      console.log(this.state);
  }

  stop(){
      console.log('ad ad');
      this.setState({stop:true})
  }



  render () {
    return (
      <div>
        <button onClick={() => this.setState({ start: true })}>start</button>
        <button onClick={() => this.setState({ stop: true })}>stop</button>

        {this.state.start && (
          <VoiceRecognition
            onStart={this.start}
            onEnd={this.end}
            onResult={this.onResult}
            continuous={true}
            lang="en-US"
            stop={this.state.stop}
          />
        )}
      </div>
    )
  }
}

export default VoiceRecognitionDemo


