const notificationsReducer = (state = {'notifications': {'answers': [], 'clubbed': [], 'upvotes': [], 'downvotes': []}}, action) => {
  switch (action.type) {
    case "DISP_NOTIFICATIONS":
      return Object.assign({}, state, {notifications: {answers: action.notifications.answers, clubbed: action.notifications.clubbed, upvotes: action.notifications.upvotes, downvotes: action.notifications.downvotes}});

    default:
      return state;
  }
};

export default notificationsReducer;


