import React, { Component } from 'react';
import {connect} from 'react-redux';
import TextareaAutosize from 'react-autosize-textarea'
import $ from 'jquery';
import {addanswer} from '../actions/questionsAction';
import './writeanswer.css';

class Writeanswer extends Component {
  constructor(props){
    super(props);
    // this.addanswer = this.addanswer.bind(this);
  }


    submitanswer(e) {//change name to toggle answers
    if(e.preventDefault){
      e.preventDefault();
    }
    console.log(this.answerRefs.value,this.props.did)
    var req = $.ajax({
      url:'/api/answerquestion',
      method:'POST',
      data: {'token': 'token', 'did': this.props.did, 'content': this.answerRefs.value}
    }).done(data=> {
      var results = JSON.parse(data);
      console.log(results)
      this.props.addanswer([e, results["msg"]]);
    });
  }


  render() {
    return (
      this.props.answerbox?
      <div className="answer-box">
          <TextareaAutosize style={{ minHeight: 40, maxHeight: 200 }}
          innerRef={ref=>{this.answerRefs=ref}}
          className="answer-content" rows={1} />
          <button
            type="button"
            className="btn btn-default submitanswer"
            onClick={this.submitanswer.bind(this,this.props.answerR.replace("answer", ""))}>
            Answer
            </button>
        </div>:null
    );
}
}


const mapStateToProps = (state) => {
  return {
  answers: state.questionsReducer.answers,
  cananswer: state.questionsReducer.cananswer,
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
    addanswer: (answer) => {
    dispatch(addanswer(answer))
  }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Writeanswer);


