import React, { Component } from 'react';
import {connect} from 'react-redux';
import Questions from './questions'
import {changetab} from '../actions/tabsAction';
import {displayquestions, sort_questions} from '../actions/questionsAction';
import $ from 'jquery';
import './tabs.css';

class Tabs extends Component {
  constructor(props){
    super(props);
    this.selecttab = this.selecttab.bind(this);
  }

  componentDidMount() {
    this.getquestions(0);
  }

  getquestions(tab) {
    console.log(this.props.curr_tab, this.props.curr_category, "CURR TAB, GET QUESTIONS CALLED");
    var req = $.ajax({
      url:'/api/getquestions',
      method:'POST',
      data: {'token': 'token', 'tab': tab, 'category': this.props.curr_category}
    }).done(data=> {
        console.log(data, "RESULTS0")
      var results = JSON.parse(data);
      console.log(results, "RESULTS")
      if (results["success"]) {
        console.log("QUESTIONS LOADED", results["msg"])
        this.props.displayquestions(results["msg"]);

      }
    });
  }

  selecttab(e) {
      if(e != this.props.curr_tab){
          console.log(this.props.curr_category, "NEW CATEGORY")
          this.props.changetab(e);
          this.props.sort_questions(e);// to display questions according to the tab selected.
          //this.getquestions(e);
      }
  }

  render() {
    return (
        <div id="tabs">
        	<ul className="nav nav-tabs nav-justified tabs">
    			<li className="active tab_i" onClick={this.selecttab.bind(this,0)}><a data-toggle="tab" href="#menu1">Recent Questions</a></li>
    			<li className="tab_i" onClick={this.selecttab.bind(this,1)}><a data-toggle="tab" href="#menu2">Most Views</a></li>
    			<li className="tab_i" onClick={this.selecttab.bind(this,2)}><a data-toggle="tab" href="#menu3">Most Answers</a></li>
    			<li className="tab_i" onClick={this.selecttab.bind(this,3)}><a data-toggle="tab" href="#menu4">Most Upvotes</a></li>
    			<li className="tab_i" onClick={this.selecttab.bind(this,4)}><a data-toggle="tab" href="#menu5">Bookmarks</a></li>
  			</ul>
  			<div className="tab-content">
    			<Questions />
  			</div>
		</div>
    );
}
}

const mapStateToProps = (state) => {
  return {
    curr_tab: state.tabsReducer.curr_tab,
    curr_category: state.categoryReducer.curr_category
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
      changetab: (tab) => {
      dispatch(changetab(tab))
      },
      sort_questions: (tab) => {
      dispatch(sort_questions(tab))
      },
      displayquestions: (questions) => {
      dispatch(displayquestions(questions))
      }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Tabs);


