import React, { Component } from 'react';
import {connect} from 'react-redux';
import {notify} from '../actions/notificationsAction';
import $ from 'jquery';
import './notifications.css';

class Notifications extends Component {
  constructor(props){
    super(props);
    this.seeNotifications = this.seeNotifications.bind(this);
  }


    seeNotifications(){
        if($("#notification-list").css('display') == 'block'){
            $("#notification-list").css({'display': 'none'});
        }
        else{
            $("#notification-list").css({'display': 'block'});
            var req = $.ajax({
            url:'/api/notifications',
                method:'GET',
                data: {'token': 'token'}
            }).done(data=> {
                var results = JSON.parse(data);
                if(results["success"] == 1) {
                    console.log(results["msg"])
                    this.props.notify(results["msg"]); 
                } 
            });
        }
    }



  render() {
    var notifications = [];

    var clubbed = [];
    for(var i=0; i<this.props.notifications.clubbed.length; i++){
        var notification_temp = [];
        for(var j=0; j<this.props.notifications.clubbed[i].notification.length; j++){
            notification_temp.push(this.props.notifications.clubbed[i].notification[j]["falana_dhikada"]);
        }
        clubbed.push(notification_temp.join(", ") + " answered the question ...");
    }

    console.log(clubbed);
    for(var i=0; i<clubbed.length; i++){
        if(i==0){
            notifications.push(
                <li className="first-notification">{clubbed[i]}</li>
            );
        }
        else{
            notifications.push(
                <li className="last-notification">{clubbed[i]}</li>
            );
        }
        
    }

    return (
        <div id='notifications' onClick={this.seeNotifications}>
            <div>Notifications</div>
            <ul className="nav nav-pills nav-stacked" id="notification-list">
                {notifications}
            </ul>
		    </div>
    );
}
}

const mapStateToProps = (state) => {
  return {
    notifications: state.notificationsReducer.notifications,
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
      notify: (notifications) => {
      dispatch(notify(notifications))
      }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Notifications);


