const questionsReducer = (state = {'questions': [], 'answers': [], 'cananswer': []}, action) => {
  console.log("Reducer called", action)
  switch (action.type) {
    case "DISP_QUESTIONS":
      return Object.assign({}, state, {questions: action.questions});

    case "DISP_ANSWERS":
      state.answers = [];
      for(var i=0; i<state.questions.length;i++) {
          state.answers.push([])
      }
      for(var i=0; i<action.answers[1].length; i++) {
          state.answers[action.answers[0]].push(action.answers[1][i]);
      }
      if (state.cananswer.indexOf(action.answers[0]) == -1) {
          state.cananswer.push(action.answers[0])
      }
      return Object.assign({}, state, {});

    case "DISA_ANSWER":
      var index = state.cananswer.indexOf(action.question);
      if (index != -1) {
          state.cananswer.splice(index, 1);
      }
      return Object.assign({}, state, {});

    case "ADD_QUESTION":
      console.log("YEAH", action)
      var dummyquestions =Array.from(state.questions);
      dummyquestions.push(action.question);
      console.log("YEAH2", dummyquestions)
      var dummyanswers =Array.from(state.answers);
      dummyanswers.push(Array());
      console.log(dummyanswers, "DUMMY ANSWERS")
      return Object.assign({}, state, {questions: dummyquestions, answers: dummyanswers});

    case "DEL_QUESTION":
      var dummyquestions =Array.from(state.questions);
      dummyquestions.splice(action.question, 1);
      var dummyanswers =Array.from(state.answers);
      dummyanswers.splice(action.question, 1);
      return Object.assign({}, state, {questions: dummyquestions, answers: dummyanswers});

    case "UPVOTE_QUESTION":
      console.log("here")
      var dummyquestions =Array.from(state.questions);
      dummyquestions[action.qid]["discussion"]["upvotes"] += action.upvote;
      return Object.assign({}, state, {questions: dummyquestions});

    case "ADD_ANSWER":
      var dummyquestions =Array.from(state.questions);
      dummyquestions[action.queid]["discussion"]["answers"] = dummyquestions[action.queid]["discussion"]["answers"] + 1;
      var dummyanswers =Array.from(state.answers);
      dummyanswers[action.queid].push(action.answer);
      return Object.assign({}, state, {questions: dummyquestions, answers: dummyanswers});

    case "DEL_ANSWER":
      var dummyquestions =Array.from(state.questions);
      dummyquestions[action.answer]["discussion"]["answers"] = dummyquestions[action.answer]["discussion"]["answers"] - 1;
      var dummyanswers =Array.from(state.answers);
      dummyanswers[action.answer].splice(action.answerno, 1);
      return Object.assign({}, state, {questions: dummyquestions, answers: dummyanswers});

    case "TOGG_BOOKMARK":
      var dummyquestions =Array.from(state.questions);
      dummyquestions[action.question]["bookmark"] = !dummyquestions[action.question]["bookmark"];
      return Object.assign({}, state, {questions: dummyquestions});

    case "VIEW_QUESTION":
      var dummyquestions =Array.from(state.questions);
      dummyquestions[action.question]["discussion"]["views"] = dummyquestions[action.question]["discussion"]["views"] + 1;
      return Object.assign({}, state, {questions: dummyquestions});

    case "SORT_QUESTIONS"://write them in separate file to eliminate confusion
      console.log(action, "ACTION");
      var dummyquestions =Array.from(state.questions);
      if(action.tab == 0 || action.tab == 4){
         dummyquestions.sort(function(a, b){return ( Date.parse(a["discussion"]["created_at"])- Date.parse(b["discussion"]["created_at"]))});
      }
      if(action.tab == 1){
         dummyquestions.sort(function(a, b){return a["discussion"]["views"]-b["discussion"]["views"]});
      }
      else if(action.tab == 2){
         dummyquestions.sort(function(a, b){return a["discussion"]["answers"]-b["discussion"]["answers"]});
      }
      else if(action.tab == 3){
         dummyquestions.sort(function(a, b){return a["discussion"]["upvotes"]-b["discussion"]["upvotes"]});
      }
      
      return Object.assign({}, state, {questions: dummyquestions});

    default:
      return state;
  }
};

export default questionsReducer;


