import React, { Component } from 'react';
import TextareaAutosize from 'react-autosize-textarea'
import {connect} from 'react-redux';
import {askquestion} from '../actions/categoryAction';
import {addquestion} from '../actions/questionsAction';
import $ from 'jquery';
import './askquestion_modal.css';

class Askquestion_modal extends Component {
  constructor(props){
    super(props);
    this.addquestion = this.addquestion.bind(this);
    this.toggle_modal = this.toggle_modal.bind(this);
    this.questionRefs = {};
  }

  addquestion(e) {
      if(e.preventDefault){
          e.preventDefault();
      }
      console.log(this.questionRefs["question"].value)
      var req = $.ajax({
          url:'/api/addquestion',
          method:'POST',
          data: {'token': 'token', 'category': this.refs.category.value, 'content': this.questionRefs["question"].value}
      }).done(data=> {
          var results = JSON.parse(data);
          if(results["success"] == 1) {
            console.log("3213");
            results["msg"]["created_at"] = 111;
            results["msg"]["updated_at"] = results["msg"]["updated_at"]["date"];
            console.log(results["msg"])
            this.props.addquestion({bookmark: 0, discussion: results["msg"]});
          }
          this.props.askquestion(0);
      });
  }

toggle_modal() {
 // $("#question_modal").toggle();//to hide
 this.props.askquestion(0);
}

render() {
    $("#que_input")
        .focus(function() {
            if (this.value === this.defaultValue) {
                this.value = '';
            }
        })
    .blur(function() {
        if (this.value === '') {
            this.value = this.defaultValue;
        }
    });

  console.log(this.props, "msg_modal")
  return (
      <div className="modal-content" id="modal-content-question">
        <div id="header" className="modal-header">
          <span>Hitesh asks...</span>
          <button type="button" className="close" data-dismiss="modal" onClick={this.toggle_modal}>&times;</button>
          <h4 className="modal-title">{this.props.sname}</h4>
        </div>
        
        <select ref="category" id="categories">
            <option value="1">Finance</option>
            <option value="2">Prob stats</option>
            <option value="3">Mercedes</option>
            <option value="4">Audi</option>
        </select>



        <TextareaAutosize style={{ minHeight: 40, maxHeight: 200 }}
        innerRef={ref=>{this.questionRefs["question"]=ref}}
        id="que-input" rows="1" defaultValue="Add your question here" />
        <div id="footer" className="modal-footer">

          


          <a id="cancel" onClick={this.toggle_modal}>Cancel</a>
          <button type="button" id="askbutton" className="btn btn-default" onClick={this.addquestion}>Ask Question</button>
        </div>
  </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    questions: state.questionsReducer.questions,
    show_modal: state.categoryReducer.show_modal
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
      askquestion: (y) => {
      dispatch(askquestion(y))
    },
    addquestion: (question) => {
      dispatch(addquestion(question))
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Askquestion_modal);


