import React, { Component } from 'react';
import {connect} from 'react-redux';
import TextareaAutosize from 'react-autosize-textarea'
import $ from 'jquery';
import Writeanswer from './writeanswer'
import QuestionI from './questioni'
import Answers from './answers'
import './questioncombined.css';

class Questioncombined extends Component {
  constructor(props){
    super(props);
    this.toggleanswer = this.toggleanswer.bind(this);
    this.state = {answerbox: false};
  }

  toggleanswer() {
      console.log("Toggled")
      this.setState({answerbox: !this.state.answerbox})
      console.log(this.state.answerbox);
  }

  render() {
    var question = this.props.question;
    var qid = this.props.qid;
    return (
      <div id="combinedquestion">
          <QuestionI question={question} qid={qid} toggleanswer={this.toggleanswer} answerbox={this.state.answerbox}/>
          <Answers qid={qid} answerbox={this.state.answerbox}/>
          <Writeanswer answerR={"answer"+qid} did={question["discussion"]["id"]} answerbox={this.state.answerbox}/>
      </div>
    );
}
}

export default Questioncombined;


