const categoryReducer = (state = {'show_modal': 0, 'curr_category': 0}, action) => {
  switch (action.type) {
    case "ASK_QUESTION":
      state.show_modal = action.y
      return Object.assign({}, state, {});

    case "CHANGE_CATEGORY":
    console.log("CATEGORY CHANGED")
      state.curr_category = action.category;
      return Object.assign({}, state, {});

    default:
      return state;
  }
};

export default categoryReducer;


