import React, { Component } from 'react';
import {connect} from 'react-redux';
import TextareaAutosize from 'react-autosize-textarea';
import {displayquestions, displayanswers, togglebookmark, disableanswer, viewquestion, upvotequestion} from '../actions/questionsAction';
import $ from 'jquery';
import Bookmark from './bookmark';
import Qoption from './qoption';
import './questioni.css'
import nl2br from 'react-newline-to-break';

class QuestionI extends Component {
    constructor(props){
        super(props);
        this.toggle_answers = this.toggle_answers.bind(this);
        this.upvote = this.upvote.bind(this);
        
    }

    componentDidMount() {
        $(".question").css({'border-bottom-left-radius': '10px'});
        $(".question").css({'border-bottom-right-radius': '10px'});
    }



    toggle_answers(e) {//change name to toggle answers
        if(e.preventDefault){
            e.preventDefault();
        }
        if($("#question-" + e).css('border-bottom-left-radius') == '10px'){
            $("#question-" + e).css({'border-bottom-left-radius': '0px'});
            $("#question-" + e).css({'border-bottom-right-radius': '0px'});
        }
        else{
            $("#question-" + e).css({'border-bottom-left-radius': '10px'});
            $("#question-" + e).css({'border-bottom-right-radius': '10px'});
        }

        //change here css property






        console.log(this.props.answerbox, "ANSWERBOX")

        if(this.props.answerbox ==false) {
        var question = this.props.question;
        var qid = this.props.qid;

        console.log("FETCH ANSWERS")

        var req = $.ajax({//why two apis???
          url:'/api/getanswers',
          method:'POST',
          data: {'token': 'token', 'did': question["discussion"]["id"]}
        }).done(data=> {
          var results = JSON.parse(data);
          console.log(results)
          this.props.displayanswers([e, results["msg"]]);

          var req = $.ajax({
            url:'/api/view',
            method:'POST',
            data: {'token': 'token', 'did': question["discussion"]["id"]}
          }).done(data=> {
            var results = JSON.parse(data);
            if(results["success"] == 1) {
              this.props.viewquestion(e); 
            } 
          });

        });
        }
        this.props.toggleanswer();
    }

  upvote() {
    var question = this.props.question;
    var qid = this.props.qid;
    var req = $.ajax({
        url:'/api/upvotequestion',
        method:'POST',
        data: {'token': 'token', 'did': question["discussion"]["id"]}
      }).done(data=> {
        var results = JSON.parse(data);
        console.log(results, "YES");
        this.props.upvotequestion({qid: qid, upvote: results["msg"]})
      });
    
  }

  render() {
      var question = this.props.question;
      var qid = this.props.qid;
      var date = ((new Date(question["discussion"]["created_at"])).getTime() - Date.now() + 330*60000)/1000;
      var days = 0;
      var hours = 0;
      var mins = 0;
      if(-date > -1500000000) {
        date = 0;
      }
      if(parseInt(-date/(24*60*60)) > 0) {
        days = parseInt(-date/(24*60*60));
      }
      if(parseInt(-date/(60*60)) > 0){
        hours = parseInt(-date/(60*60));
      }
      if(parseInt(-date/60) > 0){
        mins = parseInt(-date/60);
      }
      var time_ago = "";
      if(days > 0){
        time_ago = days + " days ago";
      }
      else if(hours > 0){
        time_ago = hours + " hours ago";
      }
      else if(mins > 0){
        time_ago = mins + " mins ago";
      }
      else{
        time_ago = " few second ago";
      }

      var question_types = ["Finance", "Prob Stats", "Falana Dikhada", "asas", "asdassdasad", "asdsad"];

      // console.log(this.props.answerbox, "THAT")
    return (
//<span className="que_no">{question["discussion"]["category"]}</span>
//<Qoption did={question["discussion"]["id"]} qid={qid}/>
//{question["discussion"]["answers"]} 

//<div id="upvote0" onClick={this.upvote}></div>
 //                     <div>{question["discussion"]["upvotes"]}</div>
 //                     <div id="downvote0"></div>
     <div key={"ques_"+qid} className="question list-group-item" id={"question-" + qid}>
              <span className="que_no">{question_types[question["discussion"]["category"] - 1]}</span>
              
              <Bookmark bookmarked={question.bookmark} qid={qid} did={question["discussion"]["id"]}/>
            <div className="question_content">{nl2br(question["discussion"]["content"])}</div>
            <div className="options">
              <div className="upvotes"><div onClick={this.upvote} className="upvote-text">Upvote</div><div className="upvote-line"></div><div className="upvote-number">{question["discussion"]["upvotes"]}</div></div>
              <div className="published-on"><a href="#">Published on {time_ago}</a></div>
              <div onClick={this.toggle_answers.bind(this,qid)} className="answers"><a><div className="pencil"></div><div className="zzz">Answer</div></a></div>
              <div className="views"><a href="#">{question["discussion"]["views"]} Views</a></div>
            </div>
            </div>
    );
}
}

const mapStateToProps = (state) => {
  return {
  questions: state.questionsReducer.questions,
  answers: state.questionsReducer.answers,
  cananswer: state.questionsReducer.cananswer,
  curr_tab: state.tabsReducer.curr_tab,
  show_modal: state.categoryReducer.show_modal
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
  displayanswers: (answers) => {
    dispatch(displayanswers(answers))
  },
  viewquestion: (question) => {
    dispatch(viewquestion(question))
  },
  upvotequestion: (question) => {
    dispatch(upvotequestion(question))
  }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(QuestionI);


