import React, { Component } from 'react';
import $ from 'jquery';

class Addquestion extends Component {
  constructor(props){
    super(props);
    this.addquestion = this.addquestion.bind(this);
  }

  addquestion(e) {
      if(e.preventDefault){
          e.preventDefault();
      }
    
      var req = $.ajax({
          url:'/api/addquestion',
          method:'POST',
          data: {'token': 'token','content': this.refs.question.value}
      }).done(data=> {
          console.log("QUESTION ADDED")
      });
  }

  render() {
    return (
      <div>
        <textarea ref="question" defaultValue="Add your question here"></textarea>
        <button onClick={this.addquestion}>Button</button>
      </div>
    );
}
}

export default Addquestion;


