export function displayquestions(questions) {
	return {
		type: "DISP_QUESTIONS",
		questions: questions
	};
}

export function viewquestion(question) {
	return {
		type: "VIEW_QUESTION",
		question: question
	};
}

export function displayanswers(answers) {
	return {
		type: "DISP_ANSWERS",
		answers: answers
	};
}

export function togglebookmark(question) {
	return {
		type: "TOGG_BOOKMARK",
		question: question
	};
}

export function disableanswer(question) {
	return {
		type: "DISA_ANSWER",
		question: question
	};
}

export function addquestion(question) {
	return {
		type: "ADD_QUESTION",
		question: question
	};
}

export function deletequestion(question) {
	return {
		type: "DEL_QUESTION",
		question: question
	};
}

export function upvotequestion(question) {
	return {
		type: "UPVOTE_QUESTION",
		qid: question.qid,
		upvote: question.upvote
	};
}

export function addanswer(answer) {
	return {
		type: "ADD_ANSWER",
		queid: answer[0],
		answer: answer[1]
	};
}

export function deleteanswer(answer) {
	return {
		type: "DEL_ANSWER",
		answer: answer[0],
		answerno: answer[1]
	};
}

export function sort_questions(tab) {
	return {
		type: "SORT_QUESTIONS",
		tab: tab
	};
}


