import React, { Component } from 'react';
import {connect} from 'react-redux';
import $ from 'jquery';
import './questions.css';
import {displayquestions, displayanswers, togglebookmark, disableanswer} from '../actions/questionsAction';
import Askquestion_modal from './askquestion_modal'
import TextareaAutosize from 'react-autosize-textarea'
import Questioncombined from './questioncombined'

class Questions extends Component {
  constructor(props){
	super(props);
	this.like = this.like.bind(this);
	this.disp_questions = this.disp_questions.bind(this);
  }

  disp_questions() {
	  if(this.props.questions) {
	  var questions = [];
	  console.log(this.props.questions);
	  for(var i=0; i < this.props.questions.length; i++) {
	  	  if(this.props.curr_tab == 4 && this.props.questions[i]["bookmark"] == 0) continue;
		  questions.push(
		  	<Questioncombined question={this.props.questions[i]} qid={i}/>
		  );	  
	  	}
	  }

	  return questions.reverse();
  }

  like(e) {//upvote
	  if(e.preventDefault){
		  e.preventDefault();
	  }
	  console.log(this.liked)     //remove this
	  if(this.liked == 0) {
		  this.liked = 1;
	  }
  }

  render() {
	// var yourques = [];
	var yourques = this.disp_questions();
	//console.log(yourques);

	return (
		<div>
			{yourques}	
		</div>
	);
	}
}

//{this.props.show_modal ? <div id="fullscreen"> <Askquestion_modal /> </div>: null}

const mapStateToProps = (state) => {
  return {
	questions: state.questionsReducer.questions,
	answers: state.questionsReducer.answers,
	cananswer: state.questionsReducer.cananswer,
	curr_tab: state.tabsReducer.curr_tab,
	show_modal: state.categoryReducer.show_modal
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
	displayquestions: (questions) => {
	  dispatch(displayquestions(questions))
	},
	displayanswers: (answers) => {
	  dispatch(displayanswers(answers))
	},
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Questions);


