import React, { Component } from 'react';
import { withRouter } from 'react-router';
import {connect} from 'react-redux';
import {askquestion, changecategory} from '../actions/categoryAction';
import {displayquestions} from '../actions/questionsAction';
import $ from 'jquery';
import './askquestion.css';

class Category extends Component {
  constructor(props){
    super(props);
    this.addscreen = this.addscreen.bind(this);
  }

  addscreen(){
    this.props.askquestion(1);
  }

  render() {
    return (
        <div id="askquestion" onClick={this.addscreen}>
            Ask Question
		    </div>
    );
}
}

const mapStateToProps = (state) => {
  return {
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
      askquestion: (y) => {
      dispatch(askquestion(y))
      }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Category);


