import React, { Component } from 'react';
import { withRouter } from 'react-router';
import {connect} from 'react-redux';
import {askquestion, changecategory} from '../actions/categoryAction';
import {displayquestions} from '../actions/questionsAction';
import $ from 'jquery';
import './category.css';

class Category extends Component {
  constructor(props){
    super(props);
    this.addscreen = this.addscreen.bind(this);
    this.selectcategory = this.selectcategory.bind(this);
  }

  addscreen(){
    this.props.askquestion(1);
  }

  getquestions(category) {
    // console.log(this.props.curr_tab, this.props.curr_category, "CURR TAB");
    console.log("Category: ", category);
    var req = $.ajax({
      url:'/api/getquestions',
      method:'POST',
      data: {'token': 'token', 'tab': this.props.tab, 'category': category}
    }).done(data=> {
        console.log(data, "RESULTS0")
      var results = JSON.parse(data);
      console.log(results, "RESULTS")
      if (results["success"]) {
        console.log("QUESTIONS LOADED", results["msg"])
        this.props.displayquestions(results["msg"]);

      }
    });
  }




  selectcategory(e) {
      if(e != this.props.curr_category){
        this.props.changecategory(e);
        this.getquestions(e);
      }
  }


  render() {
    return (
        <div id="category">



          <ul id="category_title" className="nav nav-tabs">
              <li><a href="#"> Categories </a></li>
          </ul>

            <ul className="nav nav-pills nav-stacked">
                <li className="category_i active" onClick={this.selectcategory.bind(this,0)}><a data-toggle="tab" href="#category1">All</a></li>
                <li className="category_i" onClick={this.selectcategory.bind(this,1)}><a data-toggle="tab" href="#category2">Finance</a></li>
                <li className="category_i" onClick={this.selectcategory.bind(this,2)}><a data-toggle="tab" href="#category3">Prob stats</a></li>
                <li className="category_i" onClick={this.selectcategory.bind(this,3)}><a data-toggle="tab" href="#category4">Category 4</a></li>
                <li className="category_i" onClick={this.selectcategory.bind(this,4)}><a data-toggle="tab" href="#category5">Category 5</a></li>
                <li className="category_i" onClick={this.selectcategory.bind(this,5)}><a data-toggle="tab" href="#category6">Category 6</a></li>
            </ul>
            <div className="tab-content">
          <div id="category1" className="tab-pane fade">
          </div>
          <div id="category2" className="tab-pane fade">
          </div>
          <div id="category3" className="tab-pane fade">
          </div>
          <div id="category4" className="tab-pane fade">
          </div>
          <div id="category5" className="tab-pane fade">
          </div>
          <div id="category6" className="tab-pane fade">
          </div>
        </div>
		    </div>
    );
}
}

const mapStateToProps = (state) => {
  return {
    curr_tab: state.tabsReducer.curr_tab,
    curr_category: state.categoryReducer.curr_category
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
      askquestion: (y) => {
      dispatch(askquestion(y))
      },
      changecategory: (tab) => {
      dispatch(changecategory(tab))
      },
      displayquestions: (questions) => {
      dispatch(displayquestions(questions))
      }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Category);


