import React, { Component } from 'react';
import Notifications from './notifications';
import $ from 'jquery';
import './topbar.css';

class Topbar extends Component {
  constructor(props){
    super(props);
  }



  render() {
    return (
        <div id="topbar">
            <div id="logo"></div>
            <div id="notification-wrapper">
              <Notifications />
            </div>
        </div>
    );
}
}

export default Topbar;


