import React, { Component } from 'react';
import {connect} from 'react-redux';
import {deletequestion} from '../actions/questionsAction';
import $ from 'jquery';

class Qoption extends Component {
  constructor(props){
    super(props);
    this.delete = this.delete.bind(this);
  }



  delete() {
    console.log("FIRE IN THE HOLE", this.props.did)

      // var qid = this.props.qid;
      // var question = this.props.question;


    var req = $.ajax({
      url:'/api/delquestion',
      method:'POST',
      data: {'token': 'token', 'did': this.props.did}
    }).done(data=> {
      console.log("QUESTION DELETED.!!", data)
      this.props.deletequestion(this.props.qid);
    });
  }


  render() {
    return (
        <div>
            <button onClick={this.delete}>Delete</button>
        </div>
    );
}
}


const mapStateToProps = (state) => {
  return {
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
    deletequestion: (question) => {
    dispatch(deletequestion(question))
  }
  };
};

export default connect(null, mapDispatchToProps)(Qoption);


