const tabsReducer = (state = {'curr_tab': 0}, action) => {
  switch (action.type) {
    case "CHAN_TAB":
      console.log("CHAN_TAB CALLED", action.tab);
      state.curr_tab = action.tab;
      return Object.assign({}, state, {});
      
    default:
      return state;
  }
};

export default tabsReducer;


