import React, { Component } from 'react';
import {connect} from 'react-redux';
import TextareaAutosize from 'react-autosize-textarea'
import {deleteanswer} from '../actions/questionsAction';
import $ from 'jquery';
import './answers.css';
import nl2br from 'react-newline-to-break';

class Answers extends Component {
   constructor(props){
      super(props);
      this.delete_answer = this.delete_answer.bind(this);
      this.edit_answer = this.edit_answer.bind(this);
      this.update_answer = this.update_answer.bind(this);
   }

   toggle_edit_options(e){
      if(e.preventDefault){
         e.preventDefault();
      }
      if($("#answer-edit-" + e).css('display') == 'none'){
         $("#answer-edit-" + e).css({'display': 'block'});
         $("#answer-delete-" + e).css({'display': 'block'});
         $("#answer-option-" + e).css({'opacity': 0.5});
      }
      else{
         $("#answer-edit-" + e).css({'display': 'none'});
         $("#answer-delete-" + e).css({'display': 'none'});
         $("#answer-option-" + e).css({'opacity': ''});
      }
   }

   delete_answer(e){
      if(e.preventDefault){
         e.preventDefault();
      }
      console.log("HEY", e)
      var req = $.ajax({
         url:'/api/delanswer',
         method:'POST',
         data: {'token': 'token', 'aid': e[0]}
      }).done(data=> {
         var results = JSON.parse(data);
         console.log(results);
         this.props.deleteanswer([this.props.qid, e[1]]);
         console.log(e, "E")
         this.toggle_edit_options(e[1]);
      });
   }

   update_answer(e){
      if(e.preventDefault){
         e.preventDefault();
      }
      $("#answer-update-" + e[1]).css({'display': 'none'});
      // var req = $.ajax({
      //    url:'/api/delanswer',
      //    method:'POST',
      //    data: {'token': 'token', 'aid': e[0]}
      // }).done(data=> {
      //    var results = JSON.parse(data);
      //    console.log(results);
      //    this.props.deleteanswer([this.props.qid, e[1]]);
      //    console.log(e, "E")
      //    this.toggle_edit_options(e[1]);
      // });
   }

   edit_answer(e){
      if(e.preventDefault){
         e.preventDefault();
      }
      $("#answer-content-i-" + e[1]).attr("contentEditable","true");
      $("#answer-content-i-" + e[1]).focus();
      this.toggle_edit_options(e[1]);
      $("#answer-update-" + e[1]).css({'display': 'block'});
      // this.update_answer(e);
   }

   render() {
      var qid = this.props.qid;
      var answers = [];
      if (this.props.answers.length > 0 && this.props.cananswer.indexOf(qid) != -1) {
         answers.push.apply(answers, this.props.answers[qid].map((item, k)=>{
            return <div className="answer_i">
            <div className="answer-pic"></div>
            <div className="answer-content-i" contentEditable="false" id={"answer-content-i-" + k}>
               {nl2br(item["content"])}
            </div>
            <div className="answer-option-container">
               <div className="answer-option" id={"answer-option-" + k} onClick={this.toggle_edit_options.bind(this, k)}></div>
               <div className="answer-edit" id={"answer-edit-" + k} onClick={this.edit_answer.bind(this, [item["id"], k])}>Edit</div>
               <div className="answer-delete" id={"answer-delete-" + k} onClick={this.delete_answer.bind(this, [item["id"], k])}>Delete</div>
               <div className="answer-update" id={"answer-update-" + k} onClick={this.update_answer.bind(this, [item["id"], k])}>Update</div>
            </div>
            </div>;
         }));
      }
      return (
      this.props.answerbox?
      <div>
         {answers}
        </div>:null
      );
   }
}

const mapStateToProps = (state) => {
  return {
  answers: state.questionsReducer.answers,
  cananswer: state.questionsReducer.cananswer,
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
    deleteanswer: (answer) => {
    dispatch(deleteanswer(answer))
  }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Answers);


